
import { defineComponent } from 'vue';
import { mapGetters } from 'vuex';

export interface Expert {
	phone: string;
	position: string;
	certificate: string;
	direction: string;
	misc: string;
}

export interface Client {
	phone: string;
}

export interface Admin {}

export interface User {
	name: string;
	email: string;
	password: string;
	profile: string;
	admin: Admin | null;
	client: Client | null;
	expert: Expert | null;
}

export default defineComponent({
	data() {
		return {
			user: {} as User,
			expert: {} as Expert,
			client: {} as Client,
			admin: {},
			file: '',
			isOpen: false,
		};
	},
	async mounted() {
		await this.$store.dispatch('getUsersAction');
		await this.$store.dispatch('getProfilesAction');
	},
	computed: {
		...mapGetters(['getUsers', 'getProfiles']),
	},
	methods: {
		async changeUploadImage(e: any) {
			var files = e.target.files || e.dataTransfer.files;
			if (!files.length) return;
			this.file = files[0];
		},
		async createUser() {
			const profile =
				this.user.profile == 'Администратор'
					? this.admin
					: this.user.profile == 'Эксперт'
					? this.expert
					: this.client;

			const result = await this.$store.dispatch('addUserAction', {
				user: this.user,
				profile: profile,
			});

			document.getElementById('addExpertBtn')!.click();
			await this.$store.dispatch('getUsersAction');
		},

		async deleteUser(user_id: number) {
			await this.$store.dispatch('deleteUserAction', user_id);
		},
	},
});
